.footer-component{
    
    body {
        margin: 0;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
          'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
          sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        padding:25px;
        height:100%;
      }
    .Box h1{
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-size:20px;
    }
    .Box {
        background: linear-gradient(42deg, rgb(290, 104, 153) 0%,rgb(0, 153, 204) 100%, rgb(125, 125, 190) 30%);
        bottom: 0;
        padding-top:20px;
        overflow: hidden;
    }
    
    .FooterContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 1550px;
        height: 100px;
        margin: 0 auto;
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-size: 10px;    
        padding-right: 20px;
        padding-left: 20px;
    }
    
    .Column {
        display: flex;
        flex-direction: column;
        text-align: left;
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        line-height: 0.85;
    }
    
    .Row {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
        grid-gap: 20px;
    
    }
    
    .FooterLink {
        color: #fff;
        margin-bottom: 20px;
        font-size: 15px;
        text-decoration: none;
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
    
    .Heading {
        font-size: 17px;
        color: #fff;
        margin-bottom: 20px;
        padding-bottom: 10px;
        font-weight: bold;
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        border-bottom: 1px solid #fff;
    }
    
    .Disclaimer{
        margin-bottom: 20px;
        font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-size: 15px;
        border-top: 1px solid #fff;
        padding-top:10px;
        text-align: center;
        color: white;
    }

    .FootDis{
        text-align: center;
        color: white;
    }
}